<template>
  <div class="container">
    <div class="about">
      <img :src="imgUrl" alt>
      <h3>{{title}}</h3>
    </div>
    <p>{{summary}}</p>
  </div>
</template>
<script>
import axios from "axios-jsonp-pro";
export default {
  name: "About",
  data() {
    return {
      imgUrl: "",
      title: "",
      summary: ""
    };
  },
  mounted() {
    var id = this.$route.params.id;
    var url = "https://api.douban.com/v2/movie/subject/";
    axios.jsonp(url + id).then(res => {
      this.imgUrl = res.images.small;
      this.title = res.title;
      this.summary = res.summary;
    });
  }
};
</script>

<style lang="scss" scoped>
.container{
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  .about{
    text-align: center;
    img{
      width: 270px;
      height: 400px;
      margin-bottom: 30px;
    }
    h3{
      margin-bottom: 50px;
    }
  }
  p{
    text-indent:2em;
  }
}
</style>

